<template>
  <div class="home">
    <mHeader sidebar="ture" languagebar="true" ref="child1"></mHeader>
    <div class="homeBanner">
      <Swiper></Swiper>
      <div class="homeBac1">
        <div>
          <div class="homeBac1Title">
            <i></i>
            <span>{{$t('home.goEvents')}}</span>
            <i></i>
          </div>
          <div class="readMore">
            <img src="../../images/home/readMore.png" alt="">
            <span>read more</span>
          </div>
        </div>
      </div>
    </div>
    <div class="privateParity">
      <div class="parity">
        <span>Private Parity</span>
        <p></p>
        <ul>
          <li>Birthday party</li>
          <li>Business dinner</li>
          <li>Home party</li>
          <li>Personalized service</li>
        </ul>
      </div>
    </div>
    <div class="homeMain">
      <homeScreen/>
    </div>
    <div class="partner">
      <partnerScreen/>
    </div>
    <!--<div style="font-size: .3rem; color:#fff;">
        {{tokenString}}
    </div>-->
    <mFooter/>
  </div>
</template>

<script>
  /*import {getAll} from '../api/index.js';*/
  import Swiper from './child/Swiper.vue';
  import mLoading from '../../components/mLoading.vue';
  import homeScreen from '../../page/home/child/homeScreen.vue'
  import partnerScreen from '../../page/home/child/partnerScreen.vue'
  import mHeader from '../../components/header/headerTab.vue'
  import mFooter from '../../components/footer/footer.vue'
  import { Toast } from 'vant';

  export default {
    created(){
      // this.getData();
      window.jsGetMessageFromNative = this.jsGetMessageFromNative; // 给window
      var that = this;
      this.startLoading();
    },
    mounted(){
      this.endLoading();
    },
    data(){
      return {
        loading:true,
        tokenString: '',
        tokenString1:''
      }
    },
    mounted() {
      window.addEventListener('scroll', this.handleScroll);
    },
    destroyed () {
      // 离开页面移除
      window.removeEventListener('scroll', this.handleScroll)
    },
    methods:{
      handleScroll () {
        let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
        // this.$store.dispatch('rollDistance');
        let opcaity=(scrollTop/100>1)?1:scrollTop/100;
        this.$refs.child1.headerBac(opcaity);

        // let offsetTop = document.querySelector('#searchBar').offsetTop;
        // let heightStyle =  window.getComputedStyle(this.$refs.ele).height;
        // console.log(heightStyle)
      },

      startLoading() {
        Toast.loading({
          mask: true,
          message: this.$t('other.loading')
        });
      },

      endLoading() {
        Toast.clear
      },

      jsGetMessageFromNative(data){
        // alert(typeof data)
        // this.tokenString = data;
        // 原生获取存储sessionStorage
        /*var u = navigator.userAgent;
        var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; //android终端
        var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
        if(isAndroid){
          var typeData = data
        }
        if(isiOS){
          var typeData = JSON.parse(data)
        }*/

        let typeData;
        if(typeof(data)=='string'){
          typeData = JSON.parse(data)
        }else{
          typeData = data
        }

        sessionStorage.setItem("token",typeData.token);
        sessionStorage.setItem("seqnum",typeData.seqnum);
        sessionStorage.setItem("version",typeData.version);
        sessionStorage.setItem("uid",typeData.uid);
        sessionStorage.setItem("language",typeData.language);
        // 存储到vuex
        this.$store.dispatch("",);
        this.$store.dispatch("",);
      },
      ocCallback(strings){
        this.tokenString = strings;

       /*
        var obj1 = {
          name: 'a1',
          age: 1
      }
       window.localStorage.setItem('json_obj1', json_obj1)
        console.log(JSON.parse(window.localStorage.json_obj1));
        var storage = window.sessionStorage;
        */
      }
    },
    components: {
      Swiper,
      homeScreen,
      partnerScreen,
      mHeader,
      mFooter,
      // mLoading
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../style/mixin";
  .homeBanner{
    width: 100%;
    .homeBac1{
      @include fjcl;
      width: 100%;
      background-image: url("../../images/home/homeBac1.jpg");
      background-size: 100% 100%;
      background-repeat: no-repeat;
      height: 2.64rem;
      .homeBac1Title{
        @include fjc;
        padding-bottom: .4rem;
        i{
          @include wh(.12rem,.12rem);
          background: #fff;
          border-radius: .12rem;
        }
        span{
          @include sc(.56rem,#d3c9b3);
          padding: 0 .3rem;
        }
      }
      .readMore{
        @include fjcc;
        position: relative;
        img{
          // position: absolute;
          @include wh(3.3rem, .56rem);
        }
        span{
            @include sc(.2rem,#d3c9b3);
            margin-top: -.45rem;
          }
      }
    }

  }
  .privateParity{
    width: 100%;
    .parity{
      @include fjcc;
      @include wh(100%,4rem);
      background-image: url("../../images/home/homeBac2.jpg");
      background-repeat: no-repeat;
      background-size: 100% 100%;
      span{
        @include sc(.36rem,#d3c9b3);
      }
      p{
        @include wh(1rem,1px);
        background: #d3c9b3;
        margin: .48rem 0;
      }
      ul{
        li{
          @include sc(.2rem,#d3c9b3);
          line-height: .3rem;
          text-align: center;
        }
      }
    }
  }
  .partner{
    width: 100%;
    background: #000;
  }
</style>



