<template>
  <div class="swiperImg">
    <swiper :options="swiperOption" ref="mySwiper" >
      <!-- slides -->
      <swiper-slide><img src="../../../images/home/homeBanner.jpg" alt=""></swiper-slide>
      <swiper-slide><img src="../../../images/home/homeBanner1.jpg" alt=""></swiper-slide>
      <swiper-slide><img src="../../../images/home/homeBanner.jpg" alt=""></swiper-slide>
      <!-- Optional controls -->
      <div class="swiper-pagination" slot="pagination"></div>
    </swiper>
  </div>
</template>

<script>
  export default {
    name: 'carousel',
    data() {
      return {
        swiperOption: {
          /*autoplay: {
            delay: 3000,
            stopOnLastSlide: false,
            disableOnInteraction: false,
          },*/
          // autoplay: true,
          setWrapperSize: true,
          pagination: {
            el: '.swiper-pagination'
          },
          observerParents:true,
        },
      }
    }
  }
</script>
<style scoped>
  .swiperImg{
    width: 100%;
    height: 10.3rem;
  }
  .swiperImg img{
    width: 100%;
    height: 10.3rem;
  }
</style>



